.placeholder_input {
  position: relative;
  margin-bottom: 5px;
  display: block; }
  .placeholder_input input {
    padding: 25px 20px 15px 20px;
    font-size: 1.2em;
    border: none;
    min-width: 250px;
    background: rgba(255, 255, 255, 0.98);
    outline: none; }
    .placeholder_input input.align-center {
      text-align: center; }
    .placeholder_input input.big {
      font-size: 1.5em; }
    .placeholder_input input:focus {
      background: rgba(255, 255, 255, 0.98); }
    .placeholder_input input:focus + label, .placeholder_input input.filled + label {
      top: 10px;
      left: 20px;
      font-size: .7em;
      color: #e460c3; }
  .placeholder_input label {
    position: absolute;
    top: 20px;
    left: 30px;
    color: #ccc;
    -webkit-transition: .5s all;
    transition: .5s all; }

aside {
  position: relative;
  width: 300px;
  min-height: 100vh;
  float: left;
  overflow-x: hidden;
  box-sizing: border-box;
  border-right: 1px solid #ccc;
  padding: 10px 15px; }
  @media (max-width: 991.98px) {
    aside {
      width: 60px;
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 5;
      border-color: transparent; }
      aside.opened {
        border-right: 1px solid #ccc;
        background: #f1f1f1;
        box-shadow: 1px 0 2px rgba(0, 0, 0, 0.2); }
      aside nav {
        display: none; } }
  aside.scrolled {
    width: 60px;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;
    border-color: transparent; }
    aside.scrolled.opened {
      border-right: 1px solid #ccc;
      background: #f1f1f1;
      box-shadow: 1px 0 2px rgba(0, 0, 0, 0.2);
      width: 320px; }
      aside.scrolled.opened nav {
        display: block; }
    aside.scrolled nav {
      display: none; }
    aside.scrolled #main_menu_bt {
      display: block; }
  aside #main_menu_bt svg {
    fill: #C72A9F; }
  aside .category {
    width: 100%;
    box-shadow: none;
    text-align: left;
    font-size: 14px;
    margin: 0; }
    aside .category .category_title {
      padding: 0; }
    aside .category .laminas_list {
      padding: 0; }
      aside .category .laminas_list li {
        padding: 10px 0 !important; }
  aside ul {
    margin: 0;
    padding: 0;
    list-style: none;
    margin: 0 !important; }
    aside ul > li:hover {
      background: rgba(0, 0, 0, 0.1); }
    aside ul > li > ul > li {
      padding: 20px; }
  aside .category_title_list {
    color: #C72A9F;
    font-weight: bold; }

.scroll_message {
  display: inline-block;
  width: 95%;
  text-align: center;
  margin-top: 10px;
  color: #ccc; }
  .scroll_message svg {
    -webkit-animation: shake_vertical 3s infinite;
            animation: shake_vertical 3s infinite; }

@-webkit-keyframes shake_vertical {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); } }

@keyframes shake_vertical {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); }
  50% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  100% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px); } }

