/* --------- GENERAL ---------- */
body {
  font-family: "Open Sans", Helvetica, sans-serif;
  margin: 0;
  padding: 0; }

#loginPage {
  height: 100vh;
  background: url(/static/media/background.47b2aa37.jpg);
  background-size: cover;
  font-family: 'Open Sans', sans-serif; }

.login_box {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff; }
  .login_box img {
