/* --------- MAIN ---------- */
main {
  display: flex; }

#page {
  width: calc(100vw - 300px);
  padding: 10px 5vw; }
  #page.scrolled {
    flex-wrap: nowrap;
    width: 100vw; }
    #page.scrolled #microscope {
      position: -webkit-sticky;
      position: sticky;
      top: 100px;
      z-index: 2; }
    #page.scrolled > * {
      -webkit-transition: .5s all;
      transition: .5s all; }
  @media (max-width: 991.98px) {
    #page.scrolled {
      flex-wrap: wrap;
      width: 100vw; }
      #page.scrolled #microscope {
        position: relative;
        top: auto; } }
  #page .logo {
    height: 80px;
    opacity: .8;
    margin: 10px 0; }

@media (max-width: 991.98px) {
  #page {
    width: 100vw;
    padding-left: 80px; } }

.wrap {
  box-sizing: border-box; }

/* --------- FORM ---------- */
button {
  background: #e460c3;
  border: none;
  outline: none;
  padding: 10px 20px;
  font-size: 1.2em;
  cursor: pointer;
  -webkit-transition: all .2s;
  transition: all .2s; }
  button:hover {
    background: #f869d4; }

/* --------- FOOTER ---------- */
footer#main {
  background: #ccc;
  padding: 15px 5vw;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  box-sizing: border-box; }
  footer#main div:nth-child(4) {
